// store/Context.js
import React from "react"; // on importe createContext qui servira  la cration d'un ou plusieurs contextes

/**
 * `createContext` contient 2 proprits :
 * `Provider` et `Consumer`. Nous les rendons accessibles
 * via la constante `UserContext`, et on initialise une
 * proprit par dfaut : "name" qui sera une chaine vide.
 * On exporte ce contexte afin qu'il soit exploitable par
 * d'autres composants par la suite via le `Consumer`
 */




export const Context = React.createContext({
    logged: false,
});





